.buttons {
  align-items: normal !important;
}

.buttons > button:not(:last-child) {
  border-radius: 0 !important;
}

.title {
  font-weight: 500;
  opacity: 0.6;
}

.words {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
}

.words:after {
  content: "";
  flex: auto;
}

.word {
  align-items: center;
  display: flex !important;
  font-size: 1.5em !important;
  margin-top: 10px !important;
}

.odd {
  background-color: #5993c0 !important;
  border-color: #5993c0 !important;
  color: #fff !important;
}
